import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Tyo } from '../types';
import {
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    Button,
    SelectChangeEvent,
    List,
    ListItem,
    Card,
    CardContent,
    Typography,
    Box
} from '@mui/material';

const TyoValikko: React.FC = () => {
    const [valittuAmmatti, setValittuAmmatti] = useState<string>('');
    const [ammatit, setAmmatit] = useState<string[]>([]);
    const [tyot, setTyot] = useState<Tyo[]>([]);

    useEffect(() => {
        fetch('https://gis.vantaa.fi/rest/tyopaikat/v1')
            .then(response => response.json())
            .then(data => {
                setAmmatit(data.map((item: any) => item.ammattiala));
            });
    }, []);

    useEffect(() => {
        if (valittuAmmatti) {
            fetch(`https://gis.vantaa.fi/rest/tyopaikat/v1/${valittuAmmatti}`)
                .then(response => response.json())
                .then(data => {
                    setTyot(data);
                });
        } else {
            setTyot([]);
        }
    }, [valittuAmmatti]);

    const handleAmmattiChange = (event: SelectChangeEvent<string>) => {
        setValittuAmmatti(event.target.value as string);
    };

    return (
        <Box sx={{ m: 2 }}>
            <FormControl fullWidth sx={{ mb: 2 }}>
                <InputLabel id="ammatti-select-label">Ammattiala</InputLabel>
                <Select
                    labelId="ammatti-select-label"
                    id="ammatti-select"
                    value={valittuAmmatti}
                    label="Ammattiala"
                    onChange={handleAmmattiChange}
                >
                    {ammatit.map((ammatti, index) => (
                        <MenuItem key={index} value={ammatti}>{ammatti}</MenuItem>
                    ))}
                </Select>
                {valittuAmmatti &&
                    <Button
                        variant="contained"
                        color="primary"
                        component={Link}
                        to={`/kartta?ammatti=${valittuAmmatti}`}
                        sx={{ mt: 2 }}
                    >
                        Nyt kartalla
                    </Button>
                }
            </FormControl>

            <List>
                {tyot.map((tyo, index) => (
                    <ListItem key={index} sx={{ mb: 2 }}>
                        <Card variant="outlined">
                            <CardContent>
                                <Typography variant="h6">{tyo.tyotehtava}</Typography>
                                <Typography color="textSecondary">Haku pttyy: {tyo.haku_paattyy_pvm}</Typography>
                                <Button size="small" color="primary" href={tyo.linkki} target="_blank">Listietoja</Button>
                            </CardContent>
                        </Card>
                    </ListItem>
                ))}
            </List>
        </Box>
    );
};

export default TyoValikko;
