import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';
import { Tyo } from '../types';

const useQuery = () => {
    return new URLSearchParams(useLocation().search);
};

const TyoKartta: React.FC = () => {
    const [tyot, setTyot] = useState<Tyo[]>([]);
    const query = useQuery();
    const ammatti = query.get('ammatti');

    useEffect(() => {
        if (ammatti) {
            fetch(`https://gis.vantaa.fi/rest/tyopaikat/v1/${ammatti}`)
                .then(response => response.json())
                .then(data => {
                    setTyot(data);
                });
        }
    }, [ammatti]);

    const center: [number, number] = [60.29414, 25.04099];

    return (
        <div>
            {tyot.length > 0 && (
                <MapContainer center={center} zoom={10} style={{ height: '400px', width: '100%' }}>
                    <TileLayer url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png" />
                    {tyot.map((tyo, index) => (
                        <Marker key={index} position={[tyo.y, tyo.x]}>
                            <Popup>
                                {tyo.tyotehtava}<br />
                                Haku pttyy: {tyo.haku_paattyy_pvm}
                            </Popup>
                        </Marker>
                    ))}
                </MapContainer>
            )}
        </div>
    );
};

export default TyoKartta;
