import React from 'react';
import { Typography, Container, Box } from '@mui/material';

const Aloitus: React.FC = () => {
    return (
        <Container>
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    height: '100vh', // Koko nytn korkeus
                    textAlign: 'center' // Tekstin keskittminen
                }}
            >
                <Typography variant="h4" gutterBottom>
                    Tervetuloa tynhakuun.
                </Typography>
                <Typography>
                    Valitse sinulle sopiva ammattiala "Avoimet typaikat sivulta".
                </Typography>
            </Box>
        </Container>
    );
};

export default Aloitus;
