import React from 'react';
import { HashRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { AppBar, Toolbar, Typography, Button, Container } from '@mui/material';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Aloitus from './komponentit/Aloitus';
import TyoValikko from './komponentit/TyoValikko';
import TyoKartta from './komponentit/TyoKartta';

// Mritelln teema
const teema = createTheme({
  palette: {
    primary: {
      main: '#ee7600', // Oranssi tehostevri
    },
    background: {
      default: '#ffffff',
    },
    text: {
      primary: '#424242',
      secondary: '#bdbdbd', // Vaaleampi tekstivri
    },
  },
});

const App: React.FC = () => {
  return (
    <ThemeProvider theme={teema}>
      <Router>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
              Vantaan avoimet typaikat
            </Typography>
            <Button color="inherit" component={Link} to="/">Koti</Button>
            <Button color="inherit" component={Link} to="/tyovalikko">Avoimet typaikat</Button>
          </Toolbar>
        </AppBar>
        <Container>
          <Routes>
            <Route path="/" element={<Aloitus />} />
            <Route path="/tyovalikko" element={<TyoValikko />} />
            <Route path="/kartta" element={<TyoKartta />} />
          </Routes>
        </Container>
      </Router>
    </ThemeProvider>
  );
};

export default App;
